@import url(http://fonts.googleapis.com/css?family=Rokkitt:400,700|Lato:400,300);
html,body{margin:0;box-sizing:border-box;font-family:Lato, sans-serif;background:#3f51b5}#root{height:100vh}.content{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;height:100%;overflow:auto}.content .skewed{position:absolute;top:0;bottom:0;right:0;left:0;width:100%;height:65%;background:#2c3e50;-webkit-transform:skewY(9deg);transform:skewY(9deg);-webkit-transform-origin:top right;transform-origin:top right;z-index:0}.content .formContainer{position:relative;background-color:rgba(255,255,255,0.7);border-radius:4px;width:100vw;max-width:800px;padding:0.5rem}.content .formContainer .autofill{position:absolute;padding:5px;margin:10px;top:0;right:0}.content .formContainer div{margin:0.5rem}.content header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;text-align:center;width:100%;z-index:1;color:#25966b}.content header button{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-size:1rem;color:#d5d8dc;background:#3f51b5;margin-left:2vw;margin-right:2vw}.content header button:hover{color:#000;background:#25966b}.content header button div{padding:5px}.content main{background:#d5d8dc;padding:5px 30px;-webkit-flex:1 1;flex:1 1;z-index:1;overflow-y:auto}.content main .previewContainer{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.content main .previewContainer button{padding:5px;margin:5px}.content footer{text-align:center;z-index:1}.removeDisplay{display:none}.hidden{visibility:hidden}button:hover{color:#000;background:#25966b}

.profile{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-wrap:wrap;flex-wrap:wrap;font-size:16px}.profile input{line-height:25px;width:140px;margin:0.1em}

.skills{display:-webkit-flex;display:flex;font-size:16px;list-style:none;padding:0;-webkit-flex-wrap:wrap;flex-wrap:wrap;margin-bottom:0}.skills input{line-height:25px;margin:0.2em}.skills div{display:-webkit-flex;display:flex}.skills div svg{margin:0 0.5em}.skills div svg:hover path{fill:#25966b}

.education{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-flex-wrap:wrap;flex-wrap:wrap;font-size:16px}.education .formQuestions{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center}.education .formQuestions h5{margin:0.5em 0}.education .formQuestions input{line-height:25px;width:80%;margin:0.1em}.education .formQuestions button{margin:1em 0}.education .educationAdded{position:relative;border:1px solid #000;padding:5px}.education .educationAdded svg{position:absolute;top:0;right:0;margin:5px}.education .educationAdded svg:hover path{fill:#25966b}.education ul{padding:0;margin:0;list-style:none}

.workexp{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-flex-wrap:wrap;flex-wrap:wrap;font-size:16px}.workexp .formQuestions{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center}.workexp .formQuestions h5{margin:0.5em 0}.workexp .formQuestions input{line-height:25px;width:80%;margin:0.1em}.workexp .formQuestions button{margin:1em 0}.workexp .descriptionList{margin:0;padding:0}.workexp .descriptionList div{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;padding:5px;margin:0.2rem !important;border:1px solid #000;max-height:35px;max-width:80%}.workexp .descriptionList div li{overflow:auto;word-wrap:normal}.workexp .jobsAdded{position:relative;border:1px solid #000;padding:5px}.workexp .jobsAdded svg{position:absolute;top:0;right:0;margin:5px}.workexp .jobsAdded svg:hover path{fill:#25966b}.workexp ul{list-style:none;margin:0;padding:0}

.references{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-flex-wrap:wrap;flex-wrap:wrap;font-size:16px}.references .formQuestions{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center}.references .formQuestions h5{margin:0.5em 0}.references .formQuestions input{line-height:25px;width:80%;margin:0.1em}.references .formQuestions button{margin:1em 0}.references .referencesAdded{position:relative;border:1px solid #000;padding:5px}.references .referencesAdded svg{position:absolute;top:0;right:0;margin:5px}.references .referencesAdded svg:hover path{fill:#25966b}.references ul{list-style:none;padding:0;margin:0}

.footer{display:-webkit-inline-flex;display:inline-flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:12px;padding:0.1em 1em;background:#d5d8dc;border:1px solid #2c3e50;border-radius:15px;margin:1em 0.5em 0.5em}.footer div{margin:0.1em 0.2em}.footer a{text-decoration:none}.footer .logo svg{margin:0px 0.2em;width:0.9rem;height:0.9rem}.footer .logo :hover svg path{fill:#3f51b5}

ul{padding:0 0 0 20px}p{font-size:1em;line-height:1.4em;margin:10px 0px;color:#444}h5{margin:5px 0;color:#444}.preview{width:100%;width:850px;font-size:16px;z-index:1}.preview #cv{background:#f3f3f3;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.preview #cv .mainDetails{padding:10px 35px;border-bottom:2px solid #3f51b5;background:#25966b;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.preview #cv .mainDetails #name h1{font-size:2.5em;font-weight:700;font-family:'Rokkitt', Helvetica, Arial, sans-serif;margin-bottom:-6px;-webkit-align-items:center;align-items:center;color:#fff}.preview #cv .mainDetails #contactDetails{padding-top:25px}.preview #cv .mainDetails #contactDetails ul{list-style:none;color:#fff}.preview #cv .mainDetails #contactDetails ul a{color:#fff}.preview #cv .mainDetails #contactDetails ul a:hover{color:rgba(255,255,255,0.8)}.preview #mainArea{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.preview #mainArea .keySkills{list-style-type:none;-webkit-column-count:3;column-count:3;margin-bottom:20px;font-size:1em;color:#444}.preview #mainArea .keySkills ul li{margin-bottom:3px}.preview #mainArea .sectionTitle{width:25%;padding-left:30px}.preview #mainArea .sectionTitle h1{font-family:'Rokkitt', Helvetica, Arial, sans-serif;font-style:italic;font-weight:400;font-size:1.5em;color:#3f51b5}.preview #mainArea .sectionContent{width:72.5%;padding-right:10px}.preview #mainArea .sectionContent h2{font-family:'Rokkitt', Helvetica, Arial, sans-serif;font-weight:400;font-size:1.5em;margin-bottom:-2px}.preview #mainArea .subDetails{font-size:0.8em;font-style:italic;margin-bottom:3px}.preview #mainArea section{display:-webkit-flex;display:flex;border-top:1px solid #25966b;padding:5px 0 0}.preview #mainArea section:first-child{border-top:0}.preview #mainArea section:nth-child(3){padding:5px 0 45px}.preview #mainArea section:last-child{padding:10px;border-bottom:1px solid #25966b}@-webkit-keyframes fade-in{0%{opacity:0}40%{opacity:0}100%{opacity:1}}@keyframes fade-in{0%{opacity:0}40%{opacity:0}100%{opacity:1}}.quickFadeIn{-webkit-animation-name:reset, fade-in;animation-name:reset, fade-in;-webkit-animation-duration:0.7s;animation-duration:0.7s;-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in}

